/**
 * @category   Component
 * @package    com.kiwity.gauge.gauge
 * @author     Salvador Subarroca (subarroca@gmail.com)
 **/

import { Component, Input, OnInit } from '@angular/core';

import { GaugeLabel } from './shared/gauge-label';
import { GaugeSegment } from './shared/gauge-segment';

@Component({
  selector: 'ng-gauge',
  template: `
    <svg viewBox="0 0 200 200">
      <g
        class=kw-holder
        [attr.transform]="isIE11 ? 'translate(100 100) rotate(-90)' : ''">
        <circle
          class=kw-back
          [attr.r]="bgRadius"
          [style.fill]=bgColor/>

          <g
            *ngFor="let segment of sortedSegments">
            <circle
              class=kw-mid
              [style.stroke]=segment.bgColor
              [style.strokeWidth]=segment.borderWidth
              [attr.r]=segment.computedRadius/>

              <circle
                class=kw-front

                [style.transition]="'stroke-dasharray ' + animationSecs + 's'"
                [style.stroke]=segment.color
                [style.strokeWidth]=segment.borderWidth
                [style.strokeDasharray]="segmentsLoaded ? segment.strokeProgress : segment.strokeEmptyProgress"
                [style.strokeLinecap]="rounded ? 'round' : ''"
                [attr.r]=segment.computedRadius/>
          </g>
      </g>

      <g
        transform="translate(100, 100)">
        <text
          class=kw-label
          *ngFor="let label of labels"
          [attr.x]=label.x
          [attr.y]=label.y
          [style.fill]=label.color
          [style.fontSize]=label.fontSize
          text-anchor=middle>
          {{label.text}}
        </text>
      </g>
    </svg>
  `,
  styles: [`
    .kw-mid,
    .kw-front {
      fill: transparent;
    }

    .kw-holder {
      -webkit-transform: translate(100px, 100px) rotate(-90deg);
              transform: translate(100px, 100px) rotate(-90deg);
    }
  `]
})
export class GaugeComponent implements OnInit {
  @Input() bgRadius = 100;
  @Input() bgColor: string;
  @Input() rounded = true;
  @Input() reverse = false;
  @Input() animationSecs = 0.5;

  @Input() labels: GaugeLabel[];


  @Input()
  set segments(segments: GaugeSegment[]) {
    this.segmentsLoaded = false;
    this.sortedSegments = this.sortSegments(segments);

    // wait a bit and start animation
    setTimeout(
      () => this.segmentsLoaded = true,
      0);
  }
  sortedSegments: GaugeSegment[];
  segmentsLoaded = false;

  isIE11 = /Trident.*rv[ :]*11\./.test(navigator.userAgent);



  constructor() { }

  ngOnInit() {
  }

  sortSegments(segments: GaugeSegment[]) {
    return segments && segments.sort((a: GaugeSegment, b: GaugeSegment) => {
      if (this.reverse) {
        return (a.value / a.goal > b.value / b.goal) ? 1 : -1;
      } else {
        return (a.value / a.goal > b.value / b.goal) ? -1 : 1;
      }
    });
  }
}
